div#MoviesSearch {
    div.slick-track {
        div.slick-slide div {
            margin-right: 20px !important;
        }
    }

    .slick-next {
        right: -5px !important;
    }

    div.row {
        div.col-12 {
            h6 {
                margin: 0px;
                color: $NEWSEC;
            }
        }

        div.InputSearch {
            margin-top: 10px;

            div.content {
                width: 100%;
                height: 40px;
                background-color: $BG;
                border-radius: 5px;
                display: flex;
                align-items: center;
                flex-wrap: nowrap;

                i {
                    margin-left: 20px;
                    color: $NEWSEC;
                }

                input {
                    width: 75%;
                    height: 100%;
                    margin-left: 10px;
                    outline: none;
                    border: none;
                    background-color: transparent;
                    font-size: 12px;
                    letter-spacing: 1px;
                    color: white;
                }

                div.Dropdown {
                    width: 25%;
                    height: 100%;
                    border-left: 2px solid black;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    flex-wrap: nowrap;

                    select {
                        margin-left: 10px;
                        height: 100%;
                        font-size: 12px;
                        color: $NEWSEC;
                        border-radius: 0 !important;
                        border: none;
                        background-color: transparent;
                        outline: none;
                        appearance: none;

                    }
                }
            }
        }

        div.Movies {
            margin-top: 20px;

            div.Heading {
                margin-bottom: 10px;
            }

            div.Poster {
                height: 220px;
                overflow: hidden;

                img {
                    width: 100%;
                    height: 100%;
                }
            }
        }
    }
}