div.Recommended {
    width: 100%;

    div.Slider {
        height: 130px;

        img {
            width: 100%;
            height: 100%;
            border-radius: 5px;
        }
    }

    div.slick-slide div {
        margin: 0 5px 0 0 !important;
    }
}
div.Arrows{
    width: 100%;
    padding: 10px 0;
    span{
        font-size: 12px;
        font-weight: 500;
    }   
    div.Icons{
        width: 100%;
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: nowrap;
        padding: 10px 0 !important;
        p{
            margin: 0 !important;
            font-size: 13px;
            letter-spacing: 1px;
        }
        div{
            user-select: none;
            cursor: pointer;
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: #707070;
            width: 25px;
            height: 25px;
            border-radius: 50%;
            i{
                margin: 0 !important;
                font-size: 12px !important;
            }
        }
    }
}