div.SelectTime {
    width: 95%;
    p {
        margin: 0;
        font-size: 12px;
    }

    div.Time {
        padding: 5px 0;
        width: 100%;
        display: flex;
        align-items: center;
        flex-wrap: nowrap;

        div.TimeLine {
            padding: 5px;
            display: flex;
            align-items: center;
            flex-wrap: nowrap;
            width: 100%;

            i {
                margin: 0;
                color: white;
                font-size: 18px;
            }

            span {
                width: auto;
                margin: 2px 0 0 10px;
                font-size: 11px;
                letter-spacing: 0;
            }

            div.Input {
                background-color: #393939;
                display: flex;
                align-items: center;
                overflow: hidden;
                justify-content: center;
                margin-left: 10px;
                width: 80px;
                height: 25px;
                border: 1px solid black;
                border-radius: 5px;
                color: $MAINCOLOR;

                input {
                    appearance: none;
                    padding: 0;
                    border: none;
                    background-color: transparent;
                    font-size: 12px;
                    color: $MAINCOLOR;
                    margin: 0;
                    outline: none;
                    width: 2ch !important;

                    &:nth-of-type(2) {
                        margin-left: 5px;
                        margin-right: 10px;
                    }

                    &:nth-of-type(1) {
                        margin-right: 5px;
                        margin-left: 12px;
                    }
                }

                div.counter {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    flex-direction: column;
                    background-color: $MAINCOLOR;
                    height: 100%;
                    padding: 0px 5px;

                    i {
                        margin: 0;
                        cursor: pointer;
                        font-size: 10px
                    }
                }
            }

            div.AMPM {
                margin-left: 15px;
                background-color: #393939;
                border: 1px solid black;
                border-radius: 4px;
                display: flex;
                align-items: center;
                overflow: hidden;
                justify-content: center;
                height: 25px;

                input {
                    &:checked+label {
                        background-color: #2699FB50;
                        color: $MAINCOLOR;
                    }
                }

                label {
                    display: flex;
                    align-items: center;
                    margin: 0;
                    padding: 2px 6px;
                    height: 100%;
                    cursor: pointer;
                    color: gray;
                    font-size: 12px;
                }
            }
        }
    }
}