div.styles_overlay__CLSq- {
    padding: 0;

    div.styles_modalCenter__L9F2w {
        background-color: #171D20;
        padding: 0 !important;
        border: 2px solid $MAINCOLOR;
        color: white;
        width: 100%;

        div {
            div.container {
                div.row {
                    div.heading {
                        padding: 10px 0;
                        display: flex;
                        align-items: center;
                        justify-content: center;

                        h5 {
                            margin-bottom: 0;
                            width: 90%;
                            font-weight: 400;
                            letter-spacing: 1px;
                            padding-left: 55px;
                            text-align: center;
                        }

                        i {
                            cursor: pointer;
                            user-select: none;
                            background-color: #242D31;
                            padding: 12px 16px;
                            font-size: 20px;
                            border-radius: 50%;
                            color: $MAINCOLOR;
                        }
                    }

                    div.Profile {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        flex-direction: column;

                        div.Dp {
                            width: 120px;
                            height: 120px;
                            border-radius: 50%;
                            border: 2px solid $MAINCOLOR;
                            padding: 3px;
                        }

                        p {
                            margin-top: 15px;
                        }

                        span {
                            font-size: 12px;
                            letter-spacing: 1px;
                        }

                        div.Btns {
                            button {
                                outline: none;
                                margin: 20px 0;
                                width: 190px;
                                background-color: $SECBG;
                                color: black;
                                &:nth-of-type(1) {
                                    background-color: $MAINCOLOR;
                                    margin-right: 10px;
                                }
                            }
                        }
                    }
                }
            }

            button.styles_closeButton__20ID4 {
                display: none;
            }
        }
    }
}