div.homeLeftbar {
    margin-top: 10px;
    display: flex;
    align-items: center;
    flex-direction: column;
    padding-left: 50px;
    flex-wrap: nowrap;
    justify-content: flex-end;
    position: relative;

    div.Dp {
        height: 100px;
        width: 100px;
        border-radius: 50%;
        border: 2px solid $MAINCOLOR;
        padding: 3px;

        img {
            margin-top: 0;
        }
    }

    div.Schedule {
        display: flex;
        align-items: center;
        position: relative;
    }

    p {
        margin: 0;
        margin-top: 20px;
        color: white;
        letter-spacing: 1px;
        width: 15ch;
        text-align: center;
        text-overflow: ellipsis;
    }

    span {
        margin-top: 5px;
        width: 20ch;
        text-align: center;
        font-size: 11px;
        text-overflow: ellipsis;
        color: white;
        margin-bottom: 30px;
    }

    div.Schedule {
        margin-top: 5px;

        div.Img {
            transition: all .7s;
            cursor: pointer;
            width: 40px;
            height: 40px;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 50%;
            img{
                width: 19px;
                height: 21px;
            }
        }
    }
}