nav.MainNav {
    width: 100%;
    position: relative;
    z-index: 5;
    height: 40px !important;
    display: flex;
    align-items: center;
    justify-content: center;

    div.row {
        display: flex;
        align-items: center;
        justify-content: center;

        div.icons {
            display: flex;
            align-content: center;
            justify-content: center;

            div.MainIcon {
                user-select: none;
                cursor: pointer;
                margin-right: 10px;
                position: relative;

                &:nth-last-of-type(1) {
                    display: flex;
                    align-items: center;
                    justify-content: flex-start;
                    flex-wrap: nowrap;

                    div.Icons {
                        border-color: $MAINCOLOR !important;
                    }
                }

                div.Icons {
                    width: 45px;
                    height: 45px;
                    border: 2px solid;
                    border-radius: 50%;
                    border: 2px solid #707070;
                    padding: 0;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    div.Inside {
                        width: 90%;
                        height: 90%;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        border-radius: 50%;
                        background-color: #707070;

                        img {
                            width: 45%;
                            height: 45%;
                            border-radius: 0;
                        }
                    }

                    img {
                        width: 100%;
                        height: 100%;
                        border-radius: 50%;
                    }
                }

                p {
                    margin: 0;
                    color: white;
                    font-size: 12px;
                    margin-left: 5px;
                }

                i {
                    color: white;
                    font-size: 10px;
                    margin-left: 5px;
                }
            }

        }

        div.NavItem {
            display: flex;
            align-items: center;
            flex-wrap: nowrap;

            i {
                font-size: 30px;
                color: white;
            }

            ul {
                margin: 0;
                margin-block-start: 0;
                margin-block-end: 0;
                margin-inline-start: 0px;
                margin-inline-end: 0px;
                padding-inline-start: 0px;
                margin-top: 6px;
                display: flex;
                align-items: center;
                justify-content: flex-start;

                li {
                    &:not(:nth-of-type(1)) {
                        // margin-left: 25px;
                    }

                    &:nth-of-type(1) {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        cursor: pointer;
                    }

                    i {
                        font-size: 12px;
                        margin: 0;
                        margin-left: 10px;
                    }

                    list-style: none;
                    float: left;
                    font-size: 1vw;
                    letter-spacing: 1px;
                    font-weight: 500;
                    color: white;
                    text-transform: uppercase;
                    display: flex;
                    align-items: center;
                    margin-left: 40px;

                    &:nth-of-type(1) {
                        margin-left: 0;
                    }
                }
            }
        }
    }
}

@media (max-width:768px) {
    nav {
        background-color: $BGCOLOR;

        div.Logo {
            img {
                margin-left: -15px !important;
            }
        }

        div.icons {
            margin-top: 4px;
            margin-left: -12px !important;

            div.MainIcon {
                margin: 0 !important;

                div.Icons {
                    width: 26px !important;
                    height: 26px !important;
                }

                &:not(:nth-of-type(4)) {
                    div.Icons {
                        border: none !important;

                        img {
                            width: 60% !important;
                            height: 60% !important;
                        }

                        div.Inside {
                            background-color: transparent !important;
                        }
                    }
                }
            }
        }
    }
}