div.HOMESCREEN {
    display: flex;
    min-height: 100vh;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    display: flex;
    transition: all .5s;
    align-items: center;
    flex-direction: column;
    overflow: hidden;

    &::before {
        content: "";
        width: 100%;
        min-height: 100vh;
        max-height: 100vh;
        background-color: #000;
        /* fallback for old browsers */
        background: -webkit-linear-gradient(to left, transparent 100%, #000 41%);
        /* Chrome 10-25, Safari 5.1-6 */
        background: linear-gradient(to left, transparent 10%, #000 130%);
        /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
        position: absolute;
    }

    div.row {
        display: flex;
        align-items: center;
        
        div.SoundPlay {
            display: flex;
            justify-content: flex-end;
            align-items: center;

            span {
                color: white;
                display: flex;
                align-items: center;
                font-size: 12px;
                letter-spacing: 1px;
                font-weight: 500;

                i {
                    margin-right: 10px;
                    font-size: 20px;
                }
            }
        }
    }
}



@media (max-width:768px) {
    div.HOMESCREEN {
        min-height: 500px !important;

        &::before {
        }
    }
}