div#ScheduleSteps {
    position: absolute;
    left: 0;
    top: -200px;
    left: 80px;
    z-index: 10;
    display: flex;
    align-items: center;
    overflow: hidden;
    justify-content: center;
    flex-wrap: nowrap;
    height: 540px;
    width: 0;

    i.fa-caret-left {
        color: rgba($color: #444442, $alpha: .96);
        font-size: 40px;
        position: relative;
        top: -50px;
        margin-right: -2px;
    }

    div.Content {
        width: 290px !important;
        border-radius: 6px;
        height: 100%;
        background-color: rgba($color: #444442, $alpha: .98);

        div.btns {
            display: flex;
            align-items: center !important;
            justify-content: center;
            padding: 5px 0 10px 0;

            button {
                width: 140px;
                border: none;
                font-size: 14px;
                padding: 5px 0;
                border-radius: 6px;
                background-color: $MAINCOLOR;
                outline: none;
            }
        }
    }
}