div.SocialSetting {
    div.container {
        div.row {
            div.Head {
                h5 {
                    margin-bottom: 20px;
                    letter-spacing: 1px;
                    padding-bottom: 30px !important;
                }

                p {
                    margin: 0;
                    color: white !important;
                    font-size: 13px;
                    letter-spacing: 1px;
                    margin-bottom: 30px;
                }

                div.content {
                    display: flex;
                    align-items: center;
                    flex: nowrap;

                    label.modified {
                        color: white;
                        font-size: 13px;
                        letter-spacing: 1px;
                        margin-left: 20px;
                        max-width: 32ch;
                    }

                    margin: 10px 0;
                }

                div.Button-Group {
                    margin-top: 20px;
                    display: flex;
                    justify-content: center;
                    align-content: center;
                    width: 100%;
                    flex-wrap: wrap;
                    padding-bottom: 20px;

                    button {
                        color: white;

                        font-size: 14px;

                        &:nth-of-type(1) {
                            width: 220px;
                            background-color: $MAINCOLOR;
                            color: black;
                        }
                    }
                }
            }
        }
    }
}