div#Help {
    background-color: $BG;
    min-height: 88vh;
    div#HelpRow {
        min-height: 88vh;
        background-color: $INBOXBG;
        align-items: flex-end;
        flex-direction: row;
        flex-wrap: wrap;
        div.container {
            div.row {
                display: flex;
                height: 100%;
                align-items: center;
                justify-content: center;

                div.col-10 {

                    div.row {
                        display: flex;

                        div.col-12 {
                            h5 {
                                color: $NEWSEC;
                                letter-spacing: 1px;
                                margin: 0;
                            }

                            div.SearchBar {
                                margin-top: 20px;
                                height: 40px;
                                background-color: $BG;
                                border-radius: 5px;
                                display: flex;
                                align-items: center;
                                justify-content: center;
                                flex-wrap: nowrap;

                                i {
                                    color: $NEWSEC;
                                    font-size: 20px;
                                    margin: 0 10px;
                                }

                                input {
                                    height: 100%;
                                    width: 100%;
                                    background-color: transparent;
                                    border: none;
                                    font-size: 12px;
                                    letter-spacing: 1px;
                                    color: white;
                                }
                            }
                        }

                        div.col-md-4 {
                            margin-top: 50px;
                            padding: 15px;
                            display: flex;
                            align-items: flex-start;
                            justify-content: flex-start;
                            flex-direction: column;
                            min-height: 100px;
                            height: auto;

                            h5 {
                                &:nth-of-type(1) {
                                    color: $MAINCOLOR;
                                }

                                margin: 0;
                            }

                            h6 {
                                &:nth-of-type(1) {
                                    margin-top: 30px;
                                }

                                color: $NEWSEC;
                                letter-spacing: 1px;
                                font-weight: normal;
                                text-decoration: underline;
                            }
                        }
                    }
                }
            }
        }
    }
}