div#Trending {
    .slick-slide div{
        margin-right: 0 !important;
    }
    height: 720px;
    overflow: hidden;
    div.row {
        display: flex;
        align-content: center;
        justify-content: center;

        div.col-12 {
            h6 {
                color: $NEWSEC;
                margin: 0;
            }
        }

        div.MovieContainer {
            div.Movie {
                margin-top: 20px;
                display: flex;
                align-items: center;
                justify-content: flex-start;
                flex-wrap: nowrap;

                div.img {
                    box-shadow: 0 0 20px rgba($color: #000000, $alpha: .4);
                    width: 130px;
                    height: 200px;
                    overflow: hidden;
                    border-radius: 5px;

                    img {
                        width: 100%;
                        height: 100%;
                    }
                }

                div.Detail {
                    margin-left: 20px;
                    display: flex;
                    flex-direction: column;
                    align-items: flex-start;
                    justify-content: center;

                    p {
                        margin: 0;
                        color: white;
                        font-weight: 500;
                        letter-spacing: 1px;
                        font-size: 13px;
                    }

                    span {
                        color: $NEWSEC;
                        font-size: 12px;
                        letter-spacing: .6px;

                        &:nth-of-type(1) {
                            margin: 8px 0;
                        }
                    }
                }
            }
        }
    }
}