div.Login {
    font-family: $FONTS;

    div.row {

        div.loginForm {
            background-color: $BGCOLOR;
            padding: 0;

            div.content {
                height: 100vh;
                padding: 0;
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: column;
                background-image: url("../../img/blurbg.png");
                background-size: cover;
                background-position: center;

                form {
                    width: 60%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    flex-direction: column;

                    span.error {
                        align-self: flex-start;
                        font-weight: 400;
                        font-size: 12px;
                        letter-spacing: 1px;
                        margin-top: 5px;
                        margin-left: 5px;
                    }

                    button {
                        width: 98%;
                        margin-top: 20px;
                        margin-bottom: 30px;
                        border-radius: 3px;
                        background-color: $MAINCOLOR;
                        border: none;
                        padding: 10px 0;
                        cursor: pointer;
                        outline: none;
                    }

                }

                span.forgetPassword {
                    text-decoration: underline;
                    color: white;
                    font-size: 12px;
                    letter-spacing: 1px;
                    font-weight: 300;
                }

                span.register {
                    color: white;
                    font-size: 10px;
                    font-weight: 300;
                    margin-top: 10px;
                    letter-spacing: 1px;

                    a {
                        color: white;

                    }
                }


                img {
                    margin-bottom: 100px;
                    width: 380px;
                }

                div.socialLogin {
                    margin-top: 60px;
                    width: 100%;
                    display: flex;
                    justify-content: center;
                    align-items: center;

                    div.text-Content {
                        margin-right: 10px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        flex-wrap: nowrap;

                        &:nth-of-type(3) {
                            margin-right: 0;
                        }

                        div.icon {
                            border-bottom-left-radius: 5px;
                            border-top-left-radius: 5px;
                            height: 40px;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            width: 50px;
                            background-color: $COLORDARK;
                            border-right: 2px dotted black;

                            i {
                                font-size: 12px;
                                padding: 6px;
                                border-radius: 50%;
                                border: 2px solid;
                            }
                        }

                        div.Text {
                            background-color: $MAINCOLOR;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            font-size: 12px;
                            width: 70px;
                            height: 40px;
                            border-bottom-right-radius: 5px;
                            border-top-right-radius: 5px;
                        }
                    }
                }
            }

        }
    }
}