div.MemberPayment {
    background: $RAGR;
    font-family: $FONTS;

    div.BG {
        padding-top: 30px;
        min-height: 100vh;
        height: auto;
        background-image: url("../../../img/NEWBG.png");
        background-position: center;
        background-size: cover;
        display: block;

        h3 {
            text-align: center;
            font-weight: 500;
            padding-top: 20px;
        }

        p {
            text-align: center;
            font-weight: 600;
            margin: 0;
            padding: 0;
        }

        div.container {
            width: 80%;
            margin-top: 50px;

            div.row {
                div.col-12 {
                    span.error{
                        font-size: 12px;
                    }
                    height: auto;
                    margin-top: 15px;

                    h6 {
                        margin-bottom: 30px;
                    }

                    div.Content {
                        margin-top: 15px;
                        width: 100%;
                        display: flex;
                        align-items: flex-end;
                        justify-content: space-between;
                        flex-wrap: nowrap;

                        label {
                            color: $TXT;
                            font-weight: 500;
                        }

                        input {
                            &::placeholder {
                                color: rgba($color: #fff, $alpha: .5);
                            }
                        }

                        div.CardType {
                            display: flex;
                            padding: 0px 25px 8px 20px;
                            align-items: center;
                            justify-content: center;
                            border-bottom: 1px solid rgba($color: #fff, $alpha: .3);
                        }

                        button {
                            width: 100px;
                            padding: 7px 0;
                            font-size: 12px;
                            font-weight: 500;
                        }
                    }

                }
            }
        }

        div.row {
            footer {
                margin-top: 50px;
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: column;

                button.Trial {
                    background-color: $MAINCOLOR;
                    width: 240px;
                    font-size: 14px;
                    padding: 10px 0;
                    box-shadow: 0px 0px 10px rgba($color: #000000, $alpha: .4);
                }

                button {
                    &:nth-of-type(2) {
                        margin-top: 10px;
                        font-size: 13px;
                    }
                }

                span {
                    font-weight: 500;
                    font-size: 14px;
                    margin-top: 10px;
                }
            }
        }
    }
}