div.inputs {
    width: 99%;
    border-bottom: 1px solid rgba(255, 255, 255, .25);
    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-direction: column;

    label {
        margin-top: 10px;
        margin-bottom: 0;
        color: $MAINCOLOR;
        font-size: 12px
    }

    div.input-group {
        display: flex;
        flex-wrap: nowrap;
        align-items: center;
        justify-content: center;

        input {
            margin: 4px 0;
            outline: none;
            border: none;
            font-size: 12px;
            color: white;
            letter-spacing: 1px;
            background-color: transparent;
            width: 95%;

            &::placeholder {
                letter-spacing: 1px;
            }
        }

        i {
            width: 5%;
            color: white;
        }
    }
}