.Slide {
    margin-top: 20px;
    overflow: hidden;
    height: 520px;

    div.items {
        display: flex;
        align-items: flex-start;
        flex-direction: column;
        justify-content: center;

        span {
            color: white;
            font-size: 30px;
            border-bottom: 2px solid white;

        }

        i {
            color: $MAINCOLOR;
            display: none;
            margin: 15px 10px;
            margin-bottom: 0;
            margin-top: 0;
            margin-left: 0;

            &:nth-of-type(5) {
                color: white;
            }

            ::before {
                display: initial;
            }
        }

        span.icon {
            border: none;

            i::before {
                background: rgb(42, 171, 228);
                background: linear-gradient(90deg, rgba(42, 171, 228, 1) 75%, rgba(255, 255, 255, 1) 39%);
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
                /* reset Font Awesome's display:inline-block */
            }
        }

        div.PG {
            p {
                font-size: 16px !important;
                letter-spacing: 0px !important;
                margin-left: 10px !important;
                color: #ffffff90 !important;
            }
        }

        div.Rating {
            padding-left: 10px;

            p {
                margin: 0;
                margin-top: -2px;
                font-size: 11px;
                letter-spacing: 1px;
                color: white;
                text-align: center;

                &:nth-of-type(1) {
                    font-size: 14px;
                    letter-spacing: 1px;
                }

            }
        }

        i[class^="flaticon-"]:before,
        [class*=" flaticon-"]:before,
        [class^="flaticon-"]:after,
        [class*=" flaticon-"]:after {
            margin: 0;
            font-size: 40px;
        }

        div.Head {
            display: none;
            display: flex;
            align-items: flex-start;
            justify-content: center;
            flex-direction: column;

            span {
                font-size: 15px;
                letter-spacing: 1;
                border: none;

                &:nth-of-type(2) {
                    margin-top: 5px;
                    color: $MAINCOLOR;
                }

                &:nth-of-type(3) {
                    width: 55ch;
                    margin-top: 5px;
                    font-size: 13px;
                }
            }
        }
    }

    .swiper-slide-prev {
        opacity: .4 !important;
    }

    .swiper-slide-next {
        opacity: .4 !important;
    }
}

.swiper-slide-active {
    opacity: 1 !important;
}

.swiper-slide-active {
    margin: 60px 0 !important;
    background-color: transparent !important;

    span {
        border-color: $MAINCOLOR !important;
    }

    i {
        display: block !important;

    }

    div.Rating {
        display: inline !important;
    }

    div.Head {
        display: flex !important;
    }
}

.swiper-slide {
    div.Rating {
        display: none;
    }
}

