div.ReviewHome {
    max-height: 100vh;
    min-height: 100vh;
    width: 50%;
    position: fixed;
    left: 50%;
    z-index: -1;
    transition: all .6s;
    opacity: 0;
    top: 0;
    overflow-x: hidden;

    div.row {
        transition: all .6s;

        div.CoverPhoto {
            transition: all .6s;
            width: 100%;
            height: 280px;
            background-position: center !important;
            background-size: cover !important;
            color: white !important;
            position: relative;
            display: flex;
            align-items: center;
            justify-content: center;

            i {
                color: white !important;
                position: absolute;
                top: 20px;
                left: 95%;
                z-index: 10;
                font-size: 20px;
                cursor: pointer;
            }

            div.Content {
                transition: all .6s;
                width: 100%;
                padding: 0 20px;

                span.Heading {
                    font-size: 30px;
                    transition: all .6s;
                    border-bottom: 2px solid;
                    margin-bottom: 10px;
                }

                position: relative;
                z-index: 7;
                display: flex;
                align-items: flex-start;
                justify-content: center;
                flex-direction: column;

                div.Head {
                    display: flex;
                    transition: all .6s;
                    align-items: flex-start;
                    justify-content: center;
                    flex-direction: column;

                    span {
                        transition: all .6s;
                        margin-top: 5px;
                        font-size: 12px;

                        &:nth-of-type(3) {
                            width: 45ch;
                            margin: 10px 0;
                        }
                    }
                }

                div.btnGroup {
                    margin-top: 10px;
                    transition: all .6s;

                    a {
                        font-size: 12px;
                        transition: all .6s;
                        font-weight: 500;
                        background-color: $MAINCOLOR;

                        &:nth-of-type(2) {
                            margin-left: 10px;
                        }
                    }
                }
            }

            &::before {
                width: 100%;
                height: 280px;
                content: "";
                background: rgb(0, 0, 0);
                background: linear-gradient(105deg, rgba(0, 0, 0, 1) 0%, rgba(255, 255, 255, 0) 100%);
                position: absolute;
            }
        }


        div.timeLine {
            overflow-x: hidden;
            width: 100%;

            // background-color: rgba($color: #000000, $alpha: .7);
            div.TimeLineContent {
                height: 100%;

                div.row {
                    div.btn-Container {
                        position: relative;
                        padding: 5px 10px;
                        display: flex;
                        align-items: center;
                        justify-content: space-evenly;
                        flex-wrap: wrap;

                        &::before {
                            content: "";
                            width: 100%;
                            height: 100%;
                            position: absolute;
                            left: 0;
                            top: 0;
                            z-index: -1;
                            background-position: center;
                            background-repeat: no-repeat;
                            background-size: cover;
                            background-color: transparent;
                            background-color: rgba(29, 29, 29, 0.747);
                        }

                        button {
                            font-family: $FONTS;
                            padding: 10px 20px;
                            background-color: $BTNCOLOR;
                            color: white;
                            font-size: 13px;
                            letter-spacing: .5px;
                            border: 1px solid #707070;
                            border-radius: 5px;
                            font-weight: 500;
                            margin-top: 5px;

                            &:nth-of-type(4) {
                                color: $MAINCOLOR;
                            }
                        }
                    }

                    div.ImgContainer {
                        padding: 5px 5px;
                        background-color: black;
                        display: flex;

                        img {
                            width: 120px
                        }

                        div.text {
                            margin-left: 5px;
                            background-color: #1D1D1D;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            padding: 5px;

                            p {
                                font-family: $FONTS;
                                color: #707070;
                                margin: 0;
                                font-size: 12px;
                            }
                        }

                    }

                    div.People {
                        background-color: black;
                        padding: 0 5px;

                        div.Peoples {
                            width: 100%;
                            display: flex;
                            align-items: center;
                            justify-content: space-evenly;
                            background-color: #1D1D1D;
                            flex-wrap: wrap;

                            div.Containers {
                                padding: 5px;

                                img {
                                    width: 60px;
                                    border-radius: 20px;
                                }

                                p {
                                    margin: 0;
                                    font-family: $FONTS;
                                    color: #707070;
                                    margin: 0;
                                    font-size: 12px;
                                    text-align: center;
                                    line-height: 14px;
                                    margin-top: 7px;
                                }
                            }
                        }
                    }

                    div.Footer {
                        width: 100%;
                        height: 110px;

                        img {
                            width: 100%;
                            height: 100%;
                        }
                    }

                    div.MovieSliders {
                        div.row {
                            background-color: rgba($color: #000000, $alpha: .8);
                        }

                        div.slick-current {
                            height: 100%;
                            position: relative;

                            div {
                                div.Poster {
                                    transition: all .6s;
                                    margin-top: 0 !important;
                                    height: 240px !important;
                                    margin-top: 10px !important;
                                }
                            }
                        }

                        div.row {
                            div.dropDownMovies {
                                color: white;

                                span {
                                    cursor: pointer;
                                    display: flex;
                                    align-items: center;
                                    font-size: 16px;
                                    text-transform: uppercase;
                                    margin-top: 10px;
                                    letter-spacing: 1px;

                                    i {
                                        font-size: 15px;
                                        margin-left: 15px;
                                    }
                                }
                            }

                            div.Sliders {
                                padding: 0px;
                                overflow: hidden;

                                div.Poster {
                                    border-radius: 5px;
                                    position: relative;
                                    margin-top: 30px;
                                    height: 220px;
                                    border: none !important;
                                    overflow: hidden;

                                    div.Content {
                                        position: absolute;
                                        height: 100%;
                                        top: 0;
                                        display: flex;
                                        align-items: center;
                                        justify-content: flex-end;
                                        flex-direction: column;

                                        header {
                                            width: 98%;
                                            height: 30%;
                                            display: flex;
                                            align-items: center;
                                            justify-content: space-between;

                                            div.voting {
                                                padding: 5px 0;
                                                background-color: white;
                                                border-radius: 10px;
                                                margin-right: 0 !important;

                                                div.icons {
                                                    width: 100%;
                                                    display: flex;
                                                    align-items: center;
                                                    justify-content: center;

                                                    img {
                                                        width: 18px;
                                                        height: 18px;

                                                        &:not(:nth-of-type(1)) {
                                                            margin-left: 5px;
                                                        }
                                                    }

                                                    span {
                                                        margin-left: 10px;
                                                        font-weight: 700;
                                                        font-size: 18px;
                                                    }
                                                }

                                                div.Value {
                                                    width: 100%;

                                                    p {
                                                        text-align: center;
                                                        margin: 0;
                                                        font-size: 10px;
                                                        font-weight: 500;
                                                    }
                                                }
                                            }

                                            div.Heart {
                                                margin-right: 0 !important;
                                                border: 2px solid #988479b9;
                                                display: flex;
                                                align-items: center;
                                                justify-content: center;
                                                padding: 3px;
                                                border-radius: 50%;

                                                div.Icons {
                                                    border-radius: 50%;
                                                    padding: 7px;
                                                    display: flex;
                                                    margin-right: 0 !important;
                                                    background-color: #988479b9;

                                                    i {
                                                        color: #1A2930;
                                                        font-size: 20px;
                                                    }
                                                }
                                            }
                                        }

                                        footer {
                                            padding: 4px 5px;
                                            background-color: rgba($color: #000000, $alpha: 1);
                                            width: 100%;
                                            display: flex;
                                            align-items: center;
                                            justify-content: center;

                                            p {
                                                text-align: left;
                                                margin: 0;
                                                font-size: 9px;
                                                letter-spacing: 1px;
                                                font-weight: 500;
                                                color: white;
                                                margin: 0 9px;
                                                line-height: 10px;
                                            }

                                            div.User {
                                                border-radius: 50%;
                                                border: 2px solid $MAINCOLOR;

                                                img {
                                                    border-radius: 50%;
                                                    width: 22px;
                                                }
                                            }

                                            div.group {
                                                margin-right: 0 !important;
                                                border: 2px solid #988479b9;
                                                display: flex;
                                                align-items: center;
                                                justify-content: center;
                                                padding: 3px;
                                                border-radius: 50%;

                                                div.Icons {
                                                    border-radius: 50%;
                                                    padding: 7px;
                                                    display: flex;
                                                    margin-right: 0 !important;
                                                    background-color: #988479b9;


                                                    img {
                                                        width: 20px;
                                                    }
                                                }
                                            }
                                        }
                                    }

                                    img.slide {
                                        height: 88%;
                                        width: 100%;
                                        border-top-left-radius: 5px;
                                        border-top-right-radius: 5px;
                                        border: 1px solid #707070;
                                    }
                                }
                            }
                        }
                    }

                }
            }
        }
    }
}

@media (max-width:768px) {
    div.ReviewHome {
        width: 100% !important;
        left: 0 !important;
        height: 100vh !important;

        div.row {
            div.CoverPhoto {
                i {
                    top: 40px !important;
                    left: 90% !important;
                }
            }

            div.timeLine {
                padding: 0 !important;

                div.TimeLineContent {
                    div.Img {
                        img {
                            width: 170px !important;
                            height: 100px !important;
                        }
                    }
                }
            }
        }
    }
}

.slick-slide {
    div {
        margin-right: 2px !important;
    }
}