div#HelpInside {
    background-color: $BG;
    div#HelpInsideRow {
        background-color: $INBOXBG;
        
        div.HelpContainer {
            height: 500px;
            display: flex;
            align-items: center;
            justify-content: center;

            div.col-md-8 {
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                justify-content: center;

                a {
                    text-decoration: none;
                    margin: 0;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    color: $MAINCOLOR;

                    img {
                        margin-left: 10px;
                        margin-right: 10px;
                    }

                    font-weight: 500;
                    letter-spacing: 1px
                }

                span {
                    padding: 10px;
                    color: white;
                    font-size: 13px;
                    letter-spacing: 1px;
                }

                h5 {
                    padding: 10px;
                    margin: 0;
                    margin-top: 10px;
                    color: white;
                }

                label.AccordionLabel {
                    cursor: pointer;
                    background-color: #292C33;
                    width: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    flex-wrap: nowrap;
                    padding: 2px 10px;
                    font-size: 14px;
                    user-select: none;
                    margin: 0;
                    font-weight: 500;

                    &:not(:nth-of-type(1)) {
                        margin-top: 12px;
                    }

                    span {
                        color: $MAINCOLOR;
                        font-weight: 500;
                        font-size: 15px;
                    }

                    i {
                        color: $MAINCOLOR;
                    }
                }

                div.dropDownAccord {
                    background-color: rgba(41, 44, 51, 0.5) ;
                    margin: 5px 0;
                    p {
                        color: $NEWSEC;
                        font-size: 12px;
                        padding: 20px;
                        letter-spacing: .5px;
                    }
                }
            }
        }
    }
}