div.Panel-Setting {
    div.row {
        div.Content {
            div.AccountSetting {
                div.row {
                    div.ACC {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        flex-direction: column;

                        div.btn-group {
                            align-self: center;
                            width: 50%;
                            align-items: center;
                            justify-content: center;
                            display: flex;
                            margin-top: 20px;

                            button {
                                outline: none;

                                &:nth-of-type(1) {
                                    padding: 6px 0;
                                    width: 240px;
                                    background-color: $MAINCOLOR;
                                    border-radius: 7px;
                                }
                            }
                        }

                        label.AccordionLabel {
                            cursor: pointer;
                            background-color: #404F56;
                            width: 100%;
                            display: flex;
                            align-items: center;
                            justify-content: space-between;
                            flex-wrap: nowrap;
                            padding: 10px 20px;
                            font-size: 14px;
                            user-select: none;
                            margin: 0;
                            border-radius: 7px;
                            font-weight: 500;

                            &:not(:nth-of-type(1)) {
                                margin-top: 12px;
                            }

                            span {
                                color: black;
                            }
                        }

                        label {
                            margin: 0;
                        }

                        label.modified {
                            font-size: 13px;
                            margin-left: 5px;
                        }

                        div.SubAccount {
                            div.slick-slide {
                                div {
                                    box-shadow: none;
                                    width: 100%;
                                }
                            }

                            div.Profile {
                                width: 100%;
                                display: flex !important;
                                align-items: center;
                                justify-content: center;
                                flex-direction: column;
                                height: 190px;

                                div.Dp {
                                    margin-right: 0;
                                    display: flex;
                                    align-items: center;
                                    justify-content: center;
                                    padding: 2px;
                                    border: 2px solid $MAINCOLOR;
                                    border-radius: 50%;
                                    width: 120px;
                                    height: 120px;

                                    img {
                                        width: 99%;
                                        height: 99%;
                                    }
                                }

                                p {
                                    margin: 0;
                                    font-size: 13px;
                                    letter-spacing: 0;
                                    margin-top: 10px;
                                }

                                span {

                                    cursor: pointer;
                                    margin-top: 15px;
                                    font-size: 11px;
                                    text-decoration: underline;
                                    color: $MAINCOLOR;
                                }
                            }
                        }

                        div.AddAccount {
                            div.Dp {
                                span {
                                    text-decoration: none !important;
                                    font-size: 60px !important;
                                    font-weight: 300;
                                    margin: 0 !important;
                                    width: 99%;
                                    height: 99%;
                                    display: flex;
                                    align-items: center;
                                    justify-content: center;
                                    border-radius: 50%;
                                    background-color: #383939;

                                }
                            }

                            input {
                                align-self: flex-start;
                                border: none;
                                outline: none;
                                background-color: transparent;
                                width: 100%;
                                border-bottom: 2px solid rgba($color: #fff, $alpha: 0.3)
                            }

                            p {

                                color: $MAINCOLOR;
                                align-self: flex-start;
                            }
                        }

                        div.Playback {
                            width: 100%;
                            display: flex;
                            flex-direction: column;

                            span {

                                user-select: none;
                                display: flex;
                                align-items: center;
                                justify-items: center;
                                flex-wrap: nowrap;

                                &:not(:nth-of-type(1)) {
                                    margin-top: 20px;
                                }

                                label {
                                    cursor: pointer;
                                    margin-bottom: 0;
                                    font-size: 13px;
                                    letter-spacing: 1px;

                                    &:nth-of-type(2) {
                                        margin-left: 12px;
                                    }
                                }
                            }
                        }

                        div.Content {
                            padding: 20px 10px 10px 10px;
                            display: flex;
                            color: white;
                            width: 100%;
                            flex-direction: row;
                            flex-wrap: wrap;
                            align-items: center;
                            justify-content: space-between;

                            div.Parental {
                                width: 100%;
                                display: flex;
                                align-items: flex-start;
                                justify-content: center;
                                flex-direction: column;

                                p {
                                    margin: 0;
                                }

                                div.Pin {
                                    width: 100%;
                                    display: flex;
                                    align-items: center;
                                    flex-wrap: wrap;
                                    justify-content: space-between;

                                    input[type=number]::-webkit-inner-spin-button,
                                    input[type=number]::-webkit-outer-spin-button {
                                        -webkit-appearance: none;
                                        margin: 0;
                                    }

                                    input {
                                        margin: 20px 0;
                                        color: white;
                                        font-size: 35px;
                                        width: 120px;
                                        height: 80px;
                                        padding-left: 8.5%;
                                        outline: none;
                                        border-radius: 8px;
                                        background-color: #2A383E;
                                        border: none;
                                    }
                                }

                                span {
                                    letter-spacing: 1px;
                                    font-size: 13px;
                                    margin-bottom: 20px;
                                }

                                div.inputs {
                                    div.input-group {
                                        label {
                                            margin-left: 0;
                                        }

                                        input {
                                            font-size: 19px;
                                            letter-spacing: 2px;

                                            &::placeholder {
                                                font-size: 12px;
                                            }
                                        }
                                    }
                                }
                            }

                            div.content {
                                margin-top: 20px;
                                padding-left: 10px;
                                display: flex;
                                align-items: center;
                                justify-content: flex-start;
                                flex-wrap: nowrap;
                                width: 49%;

                                &:nth-of-type(even) {
                                    padding-left: 60px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}