div.Plans {
    height: auto;

    div.container {
        height: auto;

        div.row {
            display: flex;
            align-items: center;
            height: auto;

            div.col-12 {
                justify-content: center;
                align-items: center;
                display: flex;
                margin-top: 30px;

                div.content {
                    box-shadow: 0px 0px 10px rgba($color: #000000, $alpha: .3);
                    border-radius: 5px;
                    width: 80%;
                    height: auto;
                    background-color: $BGCOLOR;
                    display: flex;
                    align-items: center;
                    justify-content: flex-start;
                    flex-direction: column;

                    header {
                        border-top-left-radius: 5px;
                        border-top-right-radius: 5px;
                        width: 100%;
                        height: 80px;
                        letter-spacing: 1px;
                        background-color: $COLORDARK;
                        display: flex;
                        align-items: center;
                        justify-content: center;

                        h4 {
                            margin-bottom: 0;
                        }
                    }

                    div.Pricing {
                        width: 100%;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        height: 100px;
                        background-color: $BGDARK;

                        div.text {
                            display: flex;
                            align-items: flex-start;
                            justify-content: center;

                            span {
                                font-size: 16px;
                                font-weight: 600;
                                color: $COLORDARK;
                                position: relative;
                                top: 5px;

                                &:nth-of-type(2) {
                                    align-self: flex-end;
                                    font-weight: 400;
                                    top: -7px;
                                    margin-left: 5px;
                                    font-size: 13px;
                                }
                            }

                            h1 {
                                margin-left: 5px;
                                margin-bottom: 0;
                                color: $COLORDARK;
                            }
                        }
                    }

                    div.Content {
                        height: 210px;
                        width: 60%;
                        display: flex;
                        flex-direction: column;
                        align-items: center;
                        justify-content: center;

                        div.text-group {
                            margin-bottom: 10px;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            flex-wrap: nowrap;
                            width: 100%;
                            border-bottom: 1px solid rgba($color: #fff, $alpha: .3);
                            padding-bottom: 3px;

                            p {
                                padding: 0;
                                letter-spacing: .6px;
                                margin: 0;
                                width: 90%;
                                font-size: 12px;
                                color: white;
                            }

                            span {
                                text-align-last: right;
                                width: 10%;
                                color: $MAINCOLOR;
                                font-size: 12px;
                            }
                        }
                    }

                    a {
                        color: white;
                        text-align: center;
                        box-shadow: 0px 0px 10px rgba($color: #000000, $alpha: .3);
                        outline: none;
                        border: none;
                        background-color: $MAINCOLOR;
                        padding: 12px 0px;
                        width: 70%;
                        border-radius: 3px;
                        position: relative;
                        font-size: 14px;
                        top: 15px;
                    }
                }
            }
        }
    }
}