div.MovieSlider {

    div.row {
        background-color: rgba($color: #000000, $alpha: .8);
    }

    div.slick-current {
        height: 100%;
        position: relative;

        div {
            div.Poster {
                transition: all .6s;
                margin-top: 0 !important;
                height: 240px !important;
                margin-top: 10px !important;
            }
        }
    }

    div.row {
        div.dropDownMovies {
            color: white;

            span {
                cursor: pointer;
                display: flex;
                align-items: center;
                font-size: 16px;
                text-transform: uppercase;
                margin-top: 10px;
                letter-spacing: 1px;

                i {
                    font-size: 15px;
                    margin-left: 15px;
                }
            }
        }

        div.Sliders {
            padding: 0px;
            overflow: hidden;

            div.Poster {
                border-radius: 5px;
                position: relative;
                margin-top: 30px;
                height: 220px;
                border: none !important;
                overflow: hidden;

                div.Content {
                    position: absolute;
                    height: 100%;
                    top: 0;
                    display: flex;
                    align-items: center;
                    justify-content: flex-end;
                    flex-direction: column;

                    header {
                        width: 98%;
                        height: 30%;
                        display: flex;
                        align-items: center;
                        justify-content: space-between;

                        div.voting {
                            padding: 5px 0;
                            background-color: white;
                            border-radius: 10px;
                            margin-right: 0 !important;

                            div.icons {
                                width: 100%;
                                display: flex;
                                align-items: center;
                                justify-content: center;

                                img {
                                    width: 18px;
                                    height: 18px;

                                    &:not(:nth-of-type(1)) {
                                        margin-left: 5px;
                                    }
                                }

                                span {
                                    margin-left: 10px;
                                    font-weight: 700;
                                    font-size: 18px;
                                }
                            }

                            div.Value {
                                width: 100%;

                                p {
                                    text-align: center;
                                    margin: 0;
                                    font-size: 10px;
                                    font-weight: 500;
                                }
                            }
                        }

                        div.Heart {
                            margin-right: 0 !important;
                            border: 2px solid #988479b9;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            padding: 3px;
                            border-radius: 50%;

                            div.Icons {
                                border-radius: 50%;
                                padding: 7px;
                                display: flex;
                                margin-right: 0 !important;
                                background-color: #988479b9;

                                i {
                                    color: #1A2930;
                                    font-size: 20px;
                                }
                            }
                        }
                    }

                    footer {
                        padding: 4px 5px;
                        background-color: rgba($color: #000000, $alpha: 1);
                        width: 100%;
                        display: flex;
                        align-items: center;
                        justify-content: center;

                        p {
                            text-align: left;
                            margin: 0;
                            font-size: 9px;
                            letter-spacing: 1px;
                            font-weight: 500;
                            color: white;
                            margin: 0 9px;
                            line-height: 10px;
                        }

                        div.User {
                            border-radius: 50%;
                            border: 2px solid $MAINCOLOR;

                            img {
                                border-radius: 50%;
                                width: 22px;
                            }
                        }

                        div.group {
                            margin-right: 0 !important;
                            border: 2px solid #988479b9;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            padding: 3px;
                            border-radius: 50%;

                            div.Icons {
                                border-radius: 50%;
                                padding: 7px;
                                display: flex;
                                margin-right: 0 !important;
                                background-color: #988479b9;


                                img {
                                    width: 20px;
                                }
                            }
                        }
                    }
                }

                img.slide {
                    height: 86%;
                    width: 100%;
                    border-top-left-radius: 5px;
                    border-top-right-radius: 5px;
                    border: 1px solid #707070;
                }
            }
        }
    }
}

.slick-slide {
    div {
        margin-right: 2px !important;
    }
}