nav.bm-item-list {
    background-color: $BGCOLOR;

    div.container-fluid {
        outline: none !important;
        padding: 0;

        div.row {
            div.closed {
                display: flex;
                align-items: center;
                justify-content: flex-end;
                padding: 20px;
                outline: none;

                span {
                    cursor: pointer;
                    outline: none;
                    width: 40px;
                    height: 40px;
                    background-color: $SECBG;
                    border-radius: 50%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    border: 2px solid $MAINCOLOR;

                    i {
                        font-size: 22px;
                        color: $MAINCOLOR;
                    }
                }
            }

            div.Dp {
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: column;

                div.ProfileImg {
                    width: 112px;
                    height: 112px;
                    border: 2px solid $MAINCOLOR;
                    border-radius: 50%;

                    img {
                        width: 100%;
                        height: 100%;
                    }
                }

                span {
                    color: white;
                    letter-spacing: 1px;
                    font-size: 15px;
                    margin-top: 5px;
                }

                p {
                    margin: 0;
                    font-size: 12px;
                    letter-spacing: 0;
                    font-weight: 500;
                    color: #999;
                }
            }

            div.MenuItems {
                margin-top: 30px;

                ul {
                    li {
                        cursor: pointer;
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        flex-wrap: nowrap;
                        padding: 8px 20px;
                        list-style: none;
                        border-top: 1px solid $MAINCOLOR;
                        transition: all .5s;

                        &:hover {
                            background-color: $MAINCOLOR;
                        }

                        &:nth-last-child(1) {
                            border-bottom: 1px solid $MAINCOLOR;
                        }

                        span {
                            width: 85%;
                            color: white;
                            font-weight: 300;
                            letter-spacing: 1px;
                        }
                    }
                }
            }
        }
    }
}

