div.SignUp {
    font-family: $FONTS;

    div.row {

        div.SignUpForm {
            background-color: $BGCOLOR;
            padding: 0;

            div.content {
                img {
                    width: 370px;
                    margin-bottom: 70px;
                }

                height: 100vh;
                padding: 0;
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: column;
                background-image: url("../../../img/blurbg.png");
                background-size: cover;
                background-position: center;

                form {
                    width: 60%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    flex-direction: column;

                    span.error {
                        align-self: flex-start;
                        font-weight: 400;
                        font-size: 12px;
                        letter-spacing: 1px;
                        margin-top: 5px;
                        margin-left: 5px;
                    }

                    div.inputs {
                        width: 99%;
                        border-bottom: 1px solid rgba(255, 255, 255, .25);
                        display: flex;
                        align-items: flex-start;
                        justify-content: center;
                        flex-direction: column;

                        label {
                            margin-top: 20px;
                            margin-bottom: 0;
                            color: $MAINCOLOR;
                            font-size: 12px
                        }

                        div.input-group {
                            display: flex;
                            flex-wrap: nowrap;
                            align-items: center;
                            justify-content: center;

                            input {
                                margin: 4px 0;
                                outline: none;
                                border: none;
                                font-size: 12px;
                                color: white;
                                letter-spacing: 1px;
                                background-color: transparent;
                                width: 95%;

                                &::placeholder {
                                    letter-spacing: 1px;
                                }
                            }

                            i {
                                color: white;
                            }
                        }
                    }


                    button,a {
                        width: 98%;
                        margin-top: 30px;
                        margin-bottom: 30px;
                        border-radius: 3px;
                        background-color: $MAINCOLOR;
                        text-align: center;
                        color: white;
                        text-decoration: none;
                        border: none;
                        padding: 10px 0;
                        cursor: pointer;
                        outline: none;
                    }

                }

                div.TNC {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    flex-direction: row;
                    flex-wrap: nowrap;

                    label {
                        margin-bottom: 0;
                        margin-right: 10px;
                    }

                    label.Terms {
                        color: white;
                        font-size: 10px;
                        font-weight: 300;
                        letter-spacing: 1px;
                    }
                }

                span.register {
                    color: white;
                    font-size: 10px;
                    font-weight: 300;
                    margin-top: 30px;
                    letter-spacing: 1px;
                }
            }
        }
    }
}