div.PopUp {
    position: absolute;
    margin-top: 0px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    left: -118px;
    opacity: .9;

    i {
        margin: 0 !important;
        font-size: 30px !important;
        color: #373737 !important;
    }

    div.Content {
        overflow: hidden;
        margin-top: -11px;
        width: 280px;
        border-radius: 10px;
        background-color: #373737;

    }

    div.msgContainer {
        display: flex;
        padding: 10px;
        align-items: center;
        justify-content: space-between;
        transition: all .5s;
        flex-wrap: nowrap;
        border-bottom: 1.5px solid #333333;

        &:hover {
            background-color: $MAINCOLOR;
            transition: all .5s;
        }

        a {
            margin: 0 !important;
            color: white;
            text-decoration: none;
            font-size: 12px;
        }

        div.Dp {
            width: 55px;
            height: 55px;
            border: 2px solid $MAINCOLOR;
            border-radius: 50%;

            img {
                border-radius: 50%;
            }
        }

        div.content {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: center;
            width: 50%;

            p {
                margin: 0 !important;
                font-size: 12px;
            }

            span {
                font-size: 10px;
                color: #AFB3BF;
                letter-spacing: .7px;
            }
        }

        p.time {
            color: #AFB3BF !important;
        }
    }
}

div.drop {
    top: 43px;
    margin-top: 0;
    margin-left: 0;
    position: absolute;
    left: -10px;

    div.Content {
        width: auto;
    }

    div.MenuContainer {
        border-bottom: 1.5px solid #333333;
        padding: 10px 50px 10px 20px;

        p {
            margin: 0;
            color: white;
            font-size: 10px;
            letter-spacing: 2px;
            text-transform: capitalize;
            padding:5px;

        }
    }
}

div.userProfile {
    top: 45px;
    left: 0;

    div.Content {
        width: auto;
    }

    div.msgContainer {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        padding: 10px;
        width: 150px;

        img {
            margin-right: 20px;
            padding-left: 10px;
        }

        p {
            margin: 0 !important;
        }
    }
}

@media (max-width:768px) {
    div.PopUp {
        margin-top: 10px;
        margin-left: -100px;

        i {
            margin-left: 187px !important;
        }
    }

    div.userProfile {
        top: 20px;

        i {
            margin-left: 100px !important;
        }

        div.msgContainer {
            width: 130px;
        }
    }
}