div.inAppChat {
    div.content {
        div.Chat {
            div.container {
                div.row {
                    .firstHead {
                        display: flex;
                        flex-direction: column !important;
                        padding-left: 20px;
                        padding-top: 10px;

                        span.Heading {
                            color: white;
                        }

                        span.lastSeen {
                            color: #6F797D;
                            font-size: 12px;
                        }

                        hr {
                            background-color: #38505B;
                            width: 100%;
                        }

                        div.Bar {
                            align-self: center;
                            align-items: center;
                            width: 90%;
                            display: flex;

                            p {
                                font-size: 10px;
                                word-wrap: nowrap;
                                margin: 0;
                                color: #38505B;
                            }
                        }
                    }

                    flex-direction: row !important;

                    div.Messages {
                        height: 500px;
                        padding: 10px 0;
                        overflow-x: hidden;

                        div.container {
                            height: auto;

                            div.row {
                                div.col-12 {
                                    display: flex;
                                    flex-wrap: nowrap;
                                    align-items: center;
                                    justify-content: flex-start;

                                    div.DpTime {
                                        display: flex;
                                        align-items: center;
                                        justify-content: center;
                                        flex-direction: column;

                                        div.dp {
                                            padding: 2px;
                                            border-radius: 50%;
                                            border: 2px solid $MAINCOLOR;
                                            width: 50px;
                                            height: 50px;

                                            img {
                                                width: 100%;
                                                height: 100%;
                                            }
                                        }

                                        span {
                                            color: #B2B7C2;
                                            font-size: 12px;
                                            margin-top: 5px;
                                        }
                                    }

                                    div.nameMsg {
                                        margin-left: 20px;
                                        display: flex;
                                        align-items: flex-start;
                                        justify-content: center;
                                        flex-direction: column;

                                        span.Name {
                                            color: #B2B7C2;
                                            font-size: 13px;
                                        }

                                        div.Msg {
                                            background-color: $MAINCOLOR;
                                            color: white;
                                            border-radius: 5px;
                                            padding: 5px 10px;
                                            margin-top: 5px;
                                            font-size: 12px !important;
                                            letter-spacing: 1px;

                                            p {
                                                margin: 0;
                                                padding: 5px
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }

                    div.Input {
                        border-top: 1px solid $MAINCOLOR;
                        padding-top: 30px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        flex-wrap: nowrap;

                        input {
                            background-color: $BG;
                            ;
                            border: none;
                            border-radius: 5px;
                            padding: 8px 0;
                            color: white;
                            outline: none;
                            width: 100%;
                            padding-left: 10px;

                            &::placeholder {
                                font-size: 12px;
                                letter-spacing: 1px;
                            }
                        }

                        button {
                            outline: none;
                            user-select: none;
                            cursor: pointer;
                            width: 50px;
                            padding: 8px 0;
                            border: none;
                            margin-left: 20px;
                            border-radius: 5px;
                            background-color: $BG;
                        }
                    }
                }
            }
        }
    }
}