div.StepsContainer {
    background: $RAGR;
    font-family: $FONTS;

    div.StepBg {
        min-height: 100vh;
        height: auto;
        background-image: url("../../../../img/NEWBG.png");
        background-position: center;
        background-size: cover;
        display: block;

        div.Container {
            padding: 20px;

            h4 {
                margin-top: 20px;
                letter-spacing: 1px;
            }

            span {
                font-size: 15px;
                font-weight: 500;
                letter-spacing: .6px;
            }

            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;

            div.Steps {
                margin: 20px 0;
                margin-left: -20px;
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: center !important;

                hr {
                    top: -8.5px;
                    width: 190px;
                    height: 1px;
                    position: relative;
                    margin-block-start: 0;
                    margin-block-end: 0;
                    margin-inline-start: 0;
                    margin-inline-end: 0;
                }

                div.StepContent {
                    display: flex;
                    align-items: center;
                    justify-content: flex-end;
                    flex-direction: column;

                    span {
                        position: relative;
                        top: 20px;
                        font-size: 11px;
                        font-weight: 600;
                    }

                    div.Content {
                        padding: 2px;
                        border: 2px solid transparent;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        border-radius: 50%;

                        div.InnerCircle {
                            width: 44px;
                            height: 44px;
                            border-radius: 50%;
                            display: flex;
                            align-items: center;
                            justify-content: center;

                            div.InsideCircle {
                                width: 6px;
                                height: 6px;
                                background-color: white;
                                border-radius: 50%;
                            }
                        }
                    }
                }
            }
        }
    }
}

@media (max-width:768px) {
    div.Steps {
        hr {
            top: 0 !important;
            width: 90px !important;
            margin-left: 0 !important;
            margin-right: 0 !important;
        }

        div.StepContent {
            span {
                display: none !important;
            }
        }
    }
}