div#Step3Schedule {
    width: 310px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    h6 {
        padding: auto 15px !important;
        align-self: flex-start;
        margin-left: 10px;
    }

    padding: 0;

    div.MovieDetail {
        margin: 15px 0;
        display: flex;
        align-items: flex-start;
        flex-wrap: nowrap;
        width: 100%;
        padding: 0 15px;

        img {
            width: 55px;
            height: 80px;
            border-radius: 5px;
            box-shadow: 0 0 5px rgba($color: #000000, $alpha: .5);
        }

        i.fa-chevron-left {
            color: $MAINCOLOR !important;
            font-size: 12px !important;
            margin: 5px 7px 0 0 !important;
            cursor: pointer;
        }

        div.Detail {
            display: flex;
            flex-direction: column;
            width: 100%;
            align-items: flex-start;
            justify-content: flex-start;

            padding: 0px 0 0 10px;

            span {
                padding: 3px 0;
                font-size: 12px;
                margin: 0 !important;
            }

            div.Timing {
                display: flex;
                flex-wrap: nowrap;
                align-items: center;
                justify-content: space-between;
                width: 100%;

                i {
                    margin: 0 !important;
                    color: gray !important;
                    font-size: 14px;

                    span {
                        color: gray !important;
                        font-family: $FONTS;
                        letter-spacing: 0 !important;
                        font-size: 12px;
                    }
                }
            }

            div.Friends {
                margin-top: 10px;
                display: flex;
                align-items: center;
                justify-content: center;

                div.Dp {
                    &:nth-of-type(even) {
                        position: relative;
                        margin: 0 -10px;
                        z-index: 1;
                    }

                    width: 30px;
                    height: 30px;
                    border-radius: 50%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    padding: 1px;

                    img {
                        width: 100%;
                        border-radius: 50%;
                        height: 100%;
                    }
                }
            }
        }
    }

    div.btns {
        padding: 10px 0 !important;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-around !important;

        button {
            background-color: #393939 !important;
            border: 1px solid $NEWSEC !important;
            color: $NEWSEC;
            font-size: 11px !important;
            width: 110px !important;
            transition: all .6s;

            &:hover {
                border-color: $MAINCOLOR !important;
                color: $MAINCOLOR !important;
            }
        }
    }

    div.Search {
        padding: 10px 15px !important;

        i {
            margin: 0 !important;
        }

        input {
            margin-left: 10px;
            font-size: 1
        }
    }

    div.friendsContainer {
        width: 100%;
        height: 223px;
        overflow: scroll;
        overflow-x: hidden;
        margin-bottom: 30px;
        div.Friend {

            padding: 10px 15px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            flex-wrap: nowrap;
            width: 100%;
            border-bottom: 1px solid black;

            div.Dp {
                width: 50px;
                height: 50px;
                padding: 1px;

                img {
                    width: 100%;
                    height: 100%;
                }
            }

            span {
                margin: 0 !important;
                width: auto;
                font-size: 13px;
                font-weight: 500;
            }

            button {
                border-radius: 3px;
                border: none;
                font-size: 12px;
                height: 30px;
                width: 70px
            }
        }
    }
}