div.Fields {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 30px;

    form {
        width: 40%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;

        div.date {
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;

            label {
                margin-top: 20px;
                margin-bottom: 0;
                color: $MAINCOLOR;
                font-weight: 500;
                letter-spacing: 1px;
                font-size: 12px;
                align-self: flex-start
            }

            div.Inputs {
                display: flex;
                align-items: center;
                justify-content: space-between;
                flex-wrap: nowrap;

                div.Year {
                    width: 40%;
                }

                div.Month {
                    width: 25%;
                }

                div.Option {
                    border-bottom: 1px solid rgba(255, 255, 255, .25);
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    flex-wrap: nowrap;

                    select {
                        width: 100%;
                        background-color: transparent;
                        border: none;
                        outline: none;
                        color: white;
                        font-size: 14px;
                        letter-spacing: 1px;
                        padding-bottom: 5px;
                        border-radius: 0;
                        appearance: none;

                        option {
                            font-size: 12px;
                            color: white;
                        }
                    }

                    i {
                        color: white;
                    }
                }
            }
        }

        input#dp {
            display: none;
        }

        span.name {
            font-size: 16px;
            margin-top: 15px;
            font-weight: 600;
        }

        label.Image {
            margin-bottom: 0;
            cursor: pointer;
            border-radius: 50%;
            border: 2px solid $MAINCOLOR;
            padding: 3px;

            div.Circle {
                display: flex;
                align-items: center;
                justify-content: center;
                background-color: $BGDARK;
                border-radius: 50%;
                width: 70px;
                height: 70px;
                padding: 10px;

                img {
                    width: 120px
                }
            }
        }

        span.error {
            align-self: flex-start;
            font-weight: 400;
            font-size: 12px;
            letter-spacing: 1px;
            margin-top: 5px;
            margin-left: 5px;
        }

        div.inputs {
            border-bottom: 1px solid rgba(255, 255, 255, .25);
            display: flex;
            align-items: flex-start;
            justify-content: center;
            flex-direction: column;

            label {
                margin-top: 20px;
                margin-bottom: 0;
                color: $MAINCOLOR;
                font-weight: 500;
                letter-spacing: 1px;
                font-size: 12px
            }

            div.input-group {
                display: flex;
                flex-wrap: nowrap;
                align-items: center;
                justify-content: center;

                input {
                    margin: 4px 0;
                    outline: none;
                    border: none;
                    font-size: 12px;
                    color: white;
                    letter-spacing: 1px;
                    background-color: transparent;
                    width: 100%;

                    &::placeholder {
                        letter-spacing: 1px;
                        color: rgba(255, 255, 255, 0.4);
                    }
                }

                i {
                    color: white;
                }
            }
        }

        button {
            text-align: center;
            color: white;
            text-decoration: none;
            margin-top: 60px;
            width: 160px;
            padding: 10px 0;
            background-color: $MAINCOLOR;
            border: none;
            font-size: 14px;
            border-radius: 5px;
            outline: none;
            box-shadow: 0 0 5px rgba($color: #000000, $alpha: .2)
        }
    }
}

@media (max-width:768px) {
    div.Fields {
        form {
            width: 90%;
        }
    }
}