div.Step1Schedule {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-direction: column;

    h6 {
        margin: 0;
        color: white;
        letter-spacing: 1px;
        padding: 20px 0 10px 0;
    }

    span {
        width: 100%;
        text-align: left;
        letter-spacing: .5px;
    }

    div.Search {
        width: 95%;
        height: 35px;
        align-self: center;
        border-radius: 20px;
        display: flex;
        align-items: center;
        background-color: #39393990 !important;
        justify-content: center;
        overflow: hidden;
        padding: 0 10px;

        i {
            font-size: 17px !important;
            color: white !important;
            margin: 0 5px !important;
        }

        input {
            padding: 7px 0;
            height: 100%;
            width: 90%;
            border: none;
            background-color: transparent;
            outline: none;
            font-size: 12px;
            color: white;

            &::placeholder {
                color: rgba(255, 255, 255, 0.781);
            }
        }
    }
}