div.Panel-Setting {
    div.row {
        div.Content {
            div.Feedback {
                div.container {
                    div.row {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        flex-direction: column;
                        margin-top: 0;

                        div.Head {
                            h5.Same {
                                color: white;
                                letter-spacing: 1px;
                                padding-bottom: 10px;
                                border-bottom: 1.5px solid rgba($color: #fff, $alpha: .3);
                            }
                        }

                        div.MSG {
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            flex-direction: column;

                            h6 {
                                align-self: flex-start;
                                color: $MAINCOLOR;
                                font-weight: 400;
                                font-size: 12px;
                                letter-spacing: 1px;
                            }

                            input {
                                width: 100%;
                                padding-bottom: 10px;
                                border: none;
                                background-color: transparent;
                                color: white;
                                border-bottom: 1.5px solid rgba($color: #fff, $alpha: .3);
                                outline: none;

                                &::placeholder {
                                    color: white;
                                    font-size: 12px;
                                    letter-spacing: 1px;
                                }
                            }

                            textarea {
                                margin-top: 40px;
                                width: 100%;
                                height: 270px;
                                background-color: $BGCOLOR;
                                border: none;
                                font-size: 13px;
                                border-radius: 7px;
                                padding: 10px;
                                outline: none;
                                color: white;

                                &::placeholder {
                                    color: rgb(148, 148, 148);
                                    font-size: 13px;
                                    letter-spacing: 1px;
                                }
                            }

                            u {
                                color: $MAINCOLOR;
                                font-size: 13px;
                                font-weight: 500;
                                letter-spacing: 1px;
                                margin-top: 10px;
                            }

                            div.Button-Group {
                                margin-top: 20px;
                                display: flex;
                                justify-content: center;
                                align-content: center;
                                width: 100%;
                                flex-wrap: wrap;
                                padding-bottom: 20px;

                                button {
                                    color: white;

                                    font-size: 14px;

                                    &:nth-of-type(1) {
                                        width: 220px;
                                        background-color: $MAINCOLOR;
                                        color: black;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}