div.Genres {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    flex-direction: column;

    h4 {
        font-size: 13px;
        font-weight: 700;
        margin-bottom: 0;
        margin-top: 20px;

        &:nth-last-of-type(1) {
            margin-bottom: 10px;
        }
    }

    div.GenresContainer {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        flex-direction: row;
        width: 100%;

        div.Containers {
            margin-top: 10px;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-wrap: nowrap;
            margin-left: 10px;

            div.Check {
                width: 40px;
                height: 50px;
                background-color: $COLORDARK;
                border-right: 2px dotted black;
                border-top-left-radius: 5px;
                border-bottom-left-radius: 5px;
                display: flex;
                align-items: center;
                justify-content: center;

                label {
                    margin-bottom: 0;
                }
            }

            div.Content {
                width: 80px;
                height: 50px;
                background-color: $MAINCOLOR;
                border-top-right-radius: 5px;
                border-bottom-right-radius: 5px;
                display: flex;
                align-items: center;
                justify-content: center;

                p {
                    margin: 0;
                    text-transform: capitalize;
                    font-size: 13px;
                    font-weight: 500;
                }
            }
        }
    }
}

div.Movies {

    div.Poster {
        height: 300px;
        overflow: hidden;
        border-radius: 10px;
        position: relative;

        img {
            width: 100%;
            height: 100%;
        }

        div.Content {
            width: 100%;
            height: 100%;
            z-index: 100;
            position: absolute;
            top: 0;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;

            div.Check {
                width: 90%;
                height: 20%;
                display: flex;
                align-items: center;
                justify-content: flex-end;
                margin-right: 0;

                label {
                    margin-bottom: 0;
                }
            }

            div.Title {
                height: 80%;
                width: 100%;
                margin-right: 0;
                display: inline-flex;
                opacity: 0;
                align-items: flex-start;
                justify-content: flex-end;
                flex-direction: column;
                background: linear-gradient(to top, rgba(0, 0, 0, 0.65) 0%, rgba(0, 0, 0, 0) 100%);
                padding: 0 20px;

                span {
                    color: white;
                    letter-spacing: 1px;
                    text-transform: uppercase;
                    font-size: 14px;
                }

                p {
                    color: white;
                    letter-spacing: 1px;
                    text-transform: uppercase;
                    font-size: 10px;
                }
            }
        }
    }
}

button.next {
    width: 220px;
    margin-top: 10px;
    margin-bottom: 30px;
    border-radius: 5px;
    background-color: $MAINCOLOR;
    border: none;
    padding: 10px 0;
    cursor: pointer;
    outline: none;
}

.slick-slide {
    div {
        margin-right: 20px;
    }
}

div.Movies {

    .slick-center {
        div {
            div {
                div.Content {
                    div.Title {
                        opacity: 1;
                    }
                }

                height: 320px;
                box-shadow: 0 0 20px rgba($color: #000000, $alpha: .5)
            }
        }
    }
}