div.inAppChat {
    div.content {
        div.friends {
            div.container-fluid {
                div.row {
                    div.heading {
                        padding: 0 7px;
                        height: 100px;
                        display: flex;
                        align-items: center;
                        span {
                            color: white;
                            color: white;
                            text-decoration: underline;
                            letter-spacing: 1px;
                            font-weight: 300;
                            font-size: 25px;
                        }
                    }
                    div.user{
                        padding: 15px 5px;
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        div.dp{
                            width: 45px;
                            height: 45px;
                            border: 2px solid $MAINCOLOR;
                            border-radius: 50%;
                            img{
                                width: 100%;
                                height: 100%;
                            }
                        }
                        div.name{
                            width: 50%;
                            p{
                                margin: 0;
                                color: white;
                                font-size: 13px;
                                font-weight: 500;
                            }
                        }
                        div.Icons{
                            img{
                                margin: 0 5px;
                            }
                        }
                    }
                }
            }
        }
    }
}