div.Panel-Setting {
    div.row {
        div.Content {
            div.Billing {
                div.container {
                    div.row {
                        margin-top: 0;

                        div.col-md-10 {

                            h5.Same {
                                color: white;
                                margin-bottom: 10px;
                                letter-spacing: 1px;
                                width: 100%;
                                span {
                                    color: $MAINCOLOR;
                                }

                                padding-bottom: 10px;
                                border-bottom: 1.5px solid rgba($color: #fff, $alpha: .3);

                            }

                            button.Same {
                                outline: none;
                                margin: 10px 0;
                                background-color: $MAINCOLOR;
                                color: white;
                                font-size: 13px;
                                width: 110px;
                                padding: 5px 0;
                            }
                        }

                        div.Head {
                            margin-bottom: 50px;
                        }

                        div.Payment {
                            display: flex;
                            align-items: flex-start;
                            justify-content: center;
                            flex-direction: column;

                            div.btn-group {
                                margin-top: 40px;
                                display: flex;
                                justify-content: center;
                                align-content: center;
                                width: 100%;
                                flex-wrap: wrap;
                                padding-bottom: 20px;

                                button {
                                    color: white;
                                    font-size: 14px;
                                    border: none;
                                    outline: none;
                                    border-radius: 4px;
                                    background-color: transparent;

                                    &:nth-of-type(1) {
                                        margin-right: 10px;
                                        width: 200px;
                                        padding: 8px;
                                        background-color: $MAINCOLOR;
                                        color: black;
                                    }
                                }
                            }

                            div.Redeem {
                                display: flex;
                                align-items: center;
                                width: 100%;
                                justify-content: flex-start;
                            }

                            span#First {
                                margin-top: 25px;
                                color: #738491;
                                font-size: 12px;
                            }

                            div.Card {
                                margin-top: 20px;
                                min-width: 50%;
                                max-width: 100%;
                                width: auto;
                                background-color: $SECBG;
                                display: flex;
                                align-items: center;
                                justify-content: space-between;
                                flex-wrap: nowrap;
                                padding: 10px 12px;
                                border-radius: 4px;

                                i {
                                    color: white;
                                }

                                span {
                                    color: white;
                                    font-size: 12px;
                                    font-weight: 500;
                                    letter-spacing: 1px;
                                    margin-left: 10px;
                                }
                            }

                            button.Same {
                                margin-top: 30px;
                                color: black;
                                width: 130px;
                                font-weight: 500;
                            }
                        }
                    }
                }
            }
        }
    }
}