footer#helpFooter {
    background-color: $BG;
    
    div.row {
        div.col-10 {
            height: 140px !important;
            align-items: center;
            justify-content: space-between;
            display: flex;
            height: 100%;
            flex-wrap: wrap;

            h2 {
                margin: 0;
                color: $NEWSEC;
            }

            button {
                border: none;
                background-color: $MAINCOLOR;
                padding: 9px 25px;
                border-radius: 5px;
                font-size: 12px;
                font-weight: 500;
                outline: none;
            }
        }
    }
}