div.Panel-Setting {
    div.row {
        div.Content {
            div.Profile {
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: column;

                label {
                    margin: 0;
                    cursor: pointer;

                    div.Dp {
                        border: 2px solid $MAINCOLOR;
                        width: 110px;
                        border-radius: 50%;
                        height: 110px;
                        overflow: hidden;
                    }
                }

                p {
                    padding: 0;
                    margin: 0;
                    color: white;
                    font-size: 18px;
                    letter-spacing: 1px;
                    margin-top: 20px;
                }

                span {
                    color: white;
                    font-size: 9px;
                    font-weight: 500;
                    margin-top: 10px;
                    letter-spacing: 1px;
                }

                form {
                    margin-top: 10px;
                    width: 80%;
                    display: flex;
                    align-items: flex-end;
                    justify-content: space-between;
                    flex-wrap: wrap;

                    div.passWord {
                        width: 49%;
                    }

                    div.inputs {
                        margin: 20px 0;
                    }

                    div.date {
                        margin: 20px 0;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        flex-direction: column;
                        width: 49%;

                        label {
                            margin-bottom: 2px;
                            color: $MAINCOLOR;
                            font-size: 12px;
                            font-weight: 400;
                            align-self: flex-start
                        }

                        div.Inputs {
                            display: flex;
                            align-items: center;
                            justify-content: space-between;
                            flex-wrap: nowrap;

                            div.Year {
                                width: 40%;
                            }

                            div.Month {
                                width: 25%;
                            }

                            div.Option {
                                border-bottom: 1px solid rgba(255, 255, 255, .25);
                                display: flex;
                                align-items: center;
                                justify-content: center;
                                flex-wrap: nowrap;

                                select {
                                    width: 100%;
                                    background-color: transparent;
                                    border: none;
                                    outline: none;
                                    color: white;
                                    font-size: 14px;
                                    letter-spacing: 1px;
                                    padding-bottom: 5px;
                                    border-radius: 0;
                                    appearance: none;

                                    option {
                                        font-size: 12px;
                                        color: white;
                                    }
                                }

                                i {
                                    color: white;
                                }
                            }
                        }
                    }

                    div.Button-Group {
                        margin-top: 20px;
                        display: flex;
                        justify-content: center;
                        align-content: center;
                        width: 100%;
                        flex-wrap: wrap;
                        padding-bottom: 20px;

                        button {
                            color: white;

                            font-size: 14px;

                            &:nth-of-type(1) {
                                width: 220px;
                                background-color: $MAINCOLOR;
                                color: black;
                            }
                        }
                    }
                }
            }
        }
    }
}

@media (max-width:768px) {
    div.Panel-Setting {
        div.row {
            div.Content {
                div.Profile {
                    form {
                        width: 95%;
                    }
                }
            }
        }
    }
}

@media (max-width:530px) {

    @media (max-width:768px) {
        div.Panel-Setting {
            div.row {
                div.Content {
                    div.Profile {
                        form {
                            div.inputs {
                                width: 100% !important;
                            }
                        }
                    }
                }
            }
        }
    }
}