div.inAppChat {
    div.content {
        div.inbox {
            border-right: 2px solid black;

            div.container-fluid {
                padding: 0;

                div.row {
                    div.heading {
                        display: flex;
                        align-items: center;
                        height: 100px;
                        background-color: $BG;

                        span {
                            margin: 0;
                            margin-left: 20px;
                            color: white;
                            text-decoration: underline;
                            letter-spacing: 1px;
                            font-weight: 300;
                            font-size: 25px;
                        }
                    }

                    div.Search {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        flex-wrap: nowrap;
                        padding: 15px;
                        color: #A4B0B6;

                        i {
                            font-size: 16px;
                        }

                        input {
                            background-color: transparent;
                            border: none;
                            outline: none;
                            width: 100%;
                            font-size: 12px;
                            margin-left: 10px;
                            color: white;
                        }

                        border-bottom: 2px solid black;
                    }

                    div.msg {
                        user-select: none;
                        cursor: pointer;
                        padding: 12px 15px;
                        border-bottom: 1px solid black;
                        background-color: $BG;
                        display: flex;
                        align-items: center;
                        justify-content: flex-start;
                        flex-wrap: nowrap;

                        div.dp {
                            width: 55px;
                            height: 55px;
                            border: 2px solid $MAINCOLOR;
                            border-radius: 50%;

                            img {
                                width: 100%;
                                height: 100%;
                                border-radius: 50%;
                            }
                        }

                        div.title {
                            margin-left: 10px;
                            display: flex;
                            align-items: flex-start;
                            justify-content: center;
                            flex-direction: column;

                            span {
                                color: white;
                                text-transform: capitalize;
                                font-size: 14px;
                                font-weight: 500;
                            }

                            p {
                                margin: 0;
                                color: #A4B0B6;
                                font-size: 10px;
                                letter-spacing: .5px;
                            }
                        }

                        div.time {
                            align-self: flex-start;
                            margin-left: 50px;
                            margin-top: 6px;

                            span {
                                color: #A4B0B6;
                                font-size: 12px;
                            }
                        }
                    }
                }
            }
        }
    }
}

div.inAppChat {
    padding: 0;
    div.content {
        padding: 0 15px;
        div.inbox {
            div.container-fluid{
                div.row{
                    div.msg{
                        padding: 10px;
                        div.dp{
                            width: 40px;
                            height: 40px;
                        }
                    }
                }
            }
        }
    }
}