div#Forget-1 {
    background-color: $TXT;
    min-height: 100vh;

    img {
        margin-top: 30px;
        margin-left: 20px;
        width: 180px;
    }

    div.row {
        div.col-12 {
            padding-top: 120px;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;

            span {
                color: white;
                letter-spacing: 1px;
                font-size: 27px;
                font-weight: 500;
            }

            hr {
                width: 35%;
                background-color: $GRC;
            }

            p {
                color: $GRC;
                letter-spacing: 1px;
                margin: 0;
                font-size: 12px;
            }

            div.inputs {
                width: 35%;

                &:nth-of-type(1) {
                    margin-top: 80px;
                    margin-bottom: 20px;
                }

                div.input-group {
                    input {
                        padding: 5px 0;
                    }
                }
            }

            button {
                width: 35%;
                background-color: $MAINCOLOR;
                border: none;
                outline: none;
                font-size: 12px;
                padding: 10px;
                border-radius: 5px;
                margin-top: 20px;
                font-weight: 500;
            }
        }
    }
}