div#Step2Schedule {
    width: 310px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    h6 {
        align-self: flex-start;
    }

    div.MovieDetail {
        margin: 15px 0;
        display: flex;
        align-items: flex-start;
        flex-wrap: nowrap;
        width: 100%;
        padding: 0;

        img {
            width: 55px;
            height: 80px;
            border-radius: 5px;
            box-shadow: 0 0 5px rgba($color: #000000, $alpha: .5);
        }

        i.fa-chevron-left {
            color: $MAINCOLOR !important;
            font-size: 12px !important;
            margin: 5px 7px 0 0 !important;
            cursor: pointer;
        }

        div.Detail {
            display: flex;
            flex-direction: column;
            width: 100%;
            align-items: flex-start;
            justify-content: flex-start;

            padding: 5px 0 0 10px;

            span {
                padding: 7px 0;
                font-size: 12px;
                margin: 0 !important;
            }

            div.Timing {
                display: flex;
                flex-wrap: nowrap;
                align-items: center;
                justify-content: space-between;
                width: 100%;

                i {
                    margin: 0 !important;
                    color: gray !important;
                    font-size: 14px;

                    span {
                        color: gray !important;
                        font-family: $FONTS;
                        letter-spacing: 0 !important;
                        font-size: 12px;
                    }
                }
            }
        }
    }
}